.contextMenu {
    padding: 10px;
    border: 1px solid #a4a4a4;
    border-top-width: 3px;
}

.contextMenu h4 {
    font-size: 18px;
}

.contextMenu .properties .header {
    width: calc(100% + 20px);
    position: relative;
    left: -10px;
    background: #cccccc;
    padding: 5px 10px;
}

.contextMenu .properties .propertyItem{

}
.contextMenu .icon-foo {
    font-size: 18px;
}

.graph-canvas-container {
    position: relative;
    border: 1px solid #ccc;
}

.graph-canvas-container .graph-canvas {

}

ul.graphin-components-toolbar-content {
    box-shadow: none !important;
    border: 1px solid #ccc;
    /*border-top: none !important;*/
    margin: 0 !important;
}
html, body{
    font-size: 14px;
}

.selectedNode {
    border-bottom: 2px solid !important;
    background: white !important;
}

.selectedNode:hover {
    color: inherit !important;
    border-bottom-color: #333;
    /*border-bottom-width: 2px;*/
}
.graphin-components-footer {
    padding-left: 20px;
    border: 1px solid #cccccc;
    width: calc(100% + 2px);
    left: -1px;
    position: absolute;
    bottom: -1px;
    height: 30px;
    opacity: 0.75;
    background: white;
}
.graphin-components-toolbar {
    position: absolute;
    box-shadow: none !important;

}

