.rightModal {
    width: 360px;
    position: absolute;
    right: -1px;
    top: 0;
    max-height: calc(100% - 28px);

    /*background: white;*/

    /*padding: 10px;*/
}
.welcomeModal {
    width: 420px;
    position: absolute;
    left: 40px;
    top: 80px;
    max-height: calc(100% - 28px);

    /*background: white;*/

    /*padding: 10px;*/
}
.rightModal.h-full{
        height: calc(100% - 28px);

}

.cursor-pointer{
    cursor: pointer;
}
ul.graphin-components-toolbar-content > li span {
    padding: 6px 16px;
    cursor: pointer;
    line-height: 10px;

}

ul.graphin-components-toolbar-content > li {
    padding: 0;
    height: 30px;
}

ul.graphin-components-toolbar-content > li .divider {
    line-height: 16px;
    color: #d9d9d9;
    border-right: 2px solid #efefef;
    height: 30px;
    padding-top: -6px;
    margin-left: 1rem;
    margin-right: 1rem;
    cursor: auto;
}


.card {
    border-radius: 0 !important;
}
